//burgermenu 
.container__burger {
  position: relative;
  width: 24px;
  height: 24px;
  margin-left:5em;
}

.burger-menu {
  position:absolute;
    height:100%;
    width:100%;
    cursor: pointer;
}

.burger-menu > span {
  display:block;
  background: #000;

}

.burger-menu {

  span {
    width:18px;
    height: 3px;
    position: relative;
    margin: 5px; 
    &:nth-child(1) {
      transition: all 0.5s;
    }
    &:nth-child(2) {
      width:9px;
      margin-left:13px;
      transition: all 0.5s;
    }
    &:nth-child(3) {
      transition: all 0.5s;
    }
  }

}

.burger-menu.open{
  span:nth-child(2) {
    display: none;
  }
  span:nth-child(1) {
    bottom:-3px;
    transform: rotate(45deg);
  }
  span:nth-child(3) {
    top:-5px; 
    transform: rotate(-45deg);
  }
}

.burger-menu::before,
.burger-menu::after {
 color: rgba(34, 34, 34, 1);
 width:18px;
 height:3px;
 
}

